export default "import React, {Component} from 'react';\nimport {GoogleMap, withGoogleMap} from 'react-google-maps';\nimport DrawingManager from 'react-google-maps/lib/components/drawing/DrawingManager';\n\n/*\n * Add <script src=\"https://maps.googleapis.com/maps/api/js\"></script> to your HTML to provide google.maps reference\n */\n\n/*\n * https://developers.google.com/maps/documentation/javascript/examples/drawing-tools\n *\n * Note: requires the Google Maps drawing API library in your script src\n *\n * Credits: thanks @idolize for the contribution!\n */\n\nconst google = window.google;\n\nconst DrawingExampleGoogleMap = withGoogleMap((props) => (\n  <GoogleMap\n    defaultZoom={15}\n    defaultCenter={new google.maps.LatLng(47.646935, -122.303763)}>\n    <DrawingManager\n      defaultDrawingMode={google.maps.drawing.OverlayType.CIRCLE}\n      defaultOptions={{\n        drawingControl: true,\n        drawingControlOptions: {\n          position: google.maps.ControlPosition.TOP_CENTER,\n          drawingModes: [\n            google.maps.drawing.OverlayType.CIRCLE,\n            google.maps.drawing.OverlayType.POLYGON,\n            google.maps.drawing.OverlayType.POLYLINE,\n            google.maps.drawing.OverlayType.RECTANGLE,\n          ],\n        },\n        circleOptions: {\n          fillColor: '#ff0000',\n          fillOpacity: 0.2,\n          strokeWeight: 3,\n          clickable: false,\n          editable: true,\n          zIndex: 1,\n        },\n        rectangleOptions: {\n          fillColor: '#ff0000',\n          fillOpacity: 0.2,\n          strokeWeight: 3,\n          clickable: false,\n          editable: true,\n          zIndex: 1,\n        },\n      }}\n    />\n  </GoogleMap>\n));\nexport default class DrawingView extends Component {\n  render() {\n    return (\n      <DrawingExampleGoogleMap\n        containerElement={\n          <div className='cr-embed-responsive cr-embed-responsive-21by9' />\n        }\n        mapElement={<div className='cr-embed-responsive-item' />}\n      />\n    );\n  }\n}\n";